
.header {
    text-align: center;
}

.content {
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-top: 20px;


    div {
        margin-bottom: 30px;
        margin-top: 5px;
    }

    .description {
        width: 500px;
        height: 300px;
    }

    label {
        font-weight: bolder;
    }


    .radio {
        display: flex;
    }
}

.ui button input {
    width: 30px;
    background-color: grey;
    border: 0px;
}


@media (max-width: 576px)   {
    .content .description {
        width: 310px;
        height: 192px;
    }

}
